import React, { useState, useCallback } from 'react';
import Sessions from './Sessions';
import SessionDetails from './SessionDetails';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import IconBack from '@material-ui/icons/ArrowBack';
import IconPerson from '@material-ui/icons/Person';
import styles from './Main.module.css';
import { User, SessionData } from './Constants';

interface Props {
    user: User;
}
// enum Mode {
//     Sessions = 'Sessions',
//     SessionDetails = 'Session Details',
// }

const Main: React.FunctionComponent<Props> = (p: Props) =>
{
    const [selectedSessions, setSelectedSessions] = useState<SessionData[] | undefined>(undefined);
    const goBack = useCallback(() => setSelectedSessions(undefined), []);

    return (
        <div className={styles.container}>
            <AppBar position="static" className={styles.appBar}>
                <Toolbar>
                    {selectedSessions && (
                        <IconButton className={styles.menuButton} color="inherit" aria-label="Menu">
                            <IconBack onClick={goBack} />
                        </IconButton>
                    )}
                    <Typography variant="h6" color="inherit">
                        {selectedSessions ? 'Session Details' : 'Sessions'}
                    </Typography>
                    <div className={styles.grow} />
                    <Button className={styles.menuButton} color="inherit" aria-label="Menu">
                        <IconPerson className={styles.person} />
                        User
                    </Button>
                </Toolbar>
            </AppBar>
            <div className={styles.main}>
                {selectedSessions && <SessionDetails user={p.user} sessions={selectedSessions} />}
                {!selectedSessions && <Sessions user={p.user} onSelectSessions={(sessions) => setSelectedSessions(sessions)} />}
            </div>
        </div>
    );
}

export default Main;