import React, { useState, useEffect, useCallback } from 'react';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import IconArrowDown from '@material-ui/icons/KeyboardArrowDown';
import IconArrowRight from '@material-ui/icons/KeyboardArrowRight';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import styles from './Sessions.module.css';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { SessionData, __DEV__ } from './Constants';
import { Button } from '@material-ui/core';

interface Props {
    user: any;
    onSelectSessions: (sessions: SessionData[]) => void;
}
interface PropsSessions extends Props {
    data: SessionData[];
    sessionInfo: SessionInfo;
}
interface SessionInfo {
    platforms: { [key: number]: string };
    ranks: { [key: number]: string };
    dates: { [key: number]: string };
    subjects: { [key: number]: string };
}
type CheckedState = { [id: number]: boolean };

enum Mode {
    Name = 'Name',
    Rank = 'Rank',
    Platform = 'Platform',
    Date = 'Date',
}

const getSortKey = (mode: string) =>
{
    return (mode === Mode.Name && 'subjects') ||
           (mode === Mode.Rank && 'ranks') ||
           (mode === Mode.Platform && 'platforms') ||
           (mode === Mode.Date && 'dates') || '';
};

const sort = (data: SessionData[], mode: Mode, search: string, rank: number, platformId: number, date: number) =>
{
    let groups: { [key: string]: SessionData[] } = {},
        results: { [key: number]: SessionData } = {},
        sortKey = getSortKey(mode);

    data.forEach((item) =>
    {
        if ( // (rank === 'All' || item.rank === rank) &&
            (platformId < 0 || item.platformId === platformId) &&
            (date < 0 || item.date === date) &&
            (!search || (item as any)[sortKey].includes(search)))
        {
            let sortVal = (item as any)[sortKey];
            if (!groups[sortVal])
            {
                groups[sortVal] = [];
            }
            groups[sortVal].push(item);
            results[item.resultId] = item;
        }
    });

    let keysSorted = Object.keys(groups).sort(),
        out = keysSorted.map(id => groups[id]);

    return {
        results,
        groups: out
    };
};

const addTab = (name: string, mode: string) =>
{
    return <Tab key={name} label={name} value={name} className={styles.tab} />
};

const addMenuItem = ({ id, name }: { id: number, name: string }) =>
{
    return <MenuItem key={id} value={id} className={styles.menuItem}>{name}</MenuItem>
};

const addSelect = (name: string, currentValue: number, setValue: (value: number) => void, values: { [key: number]: string }) =>
{
    const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
        setValue(+e.target.value);
    };

    let arr = Object.keys(values)
                    .map(key => ({ id: +key, name: values[+key] }))
                    .sort((a, b) => a.name.localeCompare(b.name));

    return (
        <div className={styles.selectWrapper}>
            <InputLabel className={styles.inputLabel} htmlFor={name}>{name}</InputLabel>
            <Select
                className={styles.select}
                value={currentValue}
                onChange={onChange}
                inputProps={{
                    name: name,
                    id: name,
                }}
            >
                {arr.map(addMenuItem)}
            </Select>
        </div>
    );
};

const renderHeader = (first: SessionData, mode: Mode, isExpanded: boolean, expand: () => void) =>
{
    let content;
    if (mode === Mode.Name)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{`${first.lastName}, ${first.firstName}`}</div>
                <div className={styles.rank}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Rank)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.rank}</div>
            </>
        );
    }
    else if (mode === Mode.Platform)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.platformName}</div>
            </>
        );
    }
    else if (mode === Mode.Date)
    {
        content = (
            <>
                <div className={styles.name} onClick={expand}>{first.dateString}</div>
            </>
        );
    }

    return (
        <div key={'h' + first.resultId} className={`${styles.row} ${styles.rowGroup}`}>
            <IconButton className={styles.menuButton} color="inherit" aria-label="Menu" onClick={expand}>
                {isExpanded ? <IconArrowDown /> : <IconArrowRight />}
            </IconButton>
            {content}
        </div>
    );
}

const SessionsComponent: React.FunctionComponent<PropsSessions> = (p: PropsSessions) =>
{
    const { data, sessionInfo, onSelectSessions } = p;
    const [search, setSearch] = useState('');
    const [mode, setMode] = useState(Mode.Name);
    const [rank, setRank] = useState(-1);
    const [platform, setPlatform] = useState(-1);
    const [date, setDate] = useState(-1);
    const [expanded, setExpanded] = useState<any>({});
    const [checked, setChecked] = useState<CheckedState>({});
    let { groups, results } = sort(data, mode, search, rank, platform, date);

    const onChangeTab = useCallback((e: any, value: any) =>
    {
        setMode(value);
    }, []);

    const onChangeSearch = useCallback((e: any) =>
    {
        setSearch(e.target.value);
    }, []);

    const onClickExpand = useCallback((key: any) =>
    {
        let newVal = Object.assign({}, expanded);
        newVal[key] = !newVal[key];

        setExpanded(newVal);
    }, [expanded]);

    const setCheckedResult = useCallback((id: number, value: boolean) =>
    {
        let newChecked = Object.assign({}, checked, { [id]: value });
        setChecked(newChecked);
    }, [checked]);

    const toggleAllChecked = useCallback(() =>
    {
        let newChecked: CheckedState = {};
        if (Object.keys(checked).length === 0)
        {
            groups.forEach(group => group.forEach(session => { newChecked[session.resultId] = true; }));
        }
        setChecked(newChecked);
    }, [checked, groups]);

    const onClickCompare = useCallback(() =>
    {
        let sessions = Object.keys(checked).map(it => results[+it]);
        if (sessions.length > 5)
        {
            let res = window.confirm(`Are you sure you want to compare ${sessions.length} sessions?`);
            if (!res)
            {
                return;
            }
        }
        onSelectSessions(sessions);
    }, [checked, onSelectSessions, results]);

    return (
        <>
            <Tabs className={styles.tabs} value={mode} onChange={onChangeTab}>
                {addTab(Mode.Name, mode)}
                {addTab(Mode.Rank, mode)}
                {addTab(Mode.Platform, mode)}
                {addTab(Mode.Date, mode)}
            </Tabs>
            <Paper>
                {!data && <span>Loading...</span>}
                {data && (
                    <div>
                        <div className={`${styles.row} ${styles.headerRow}`}>
                            <Checkbox key="checkbox" color="primary" checked={Object.keys(checked).length > 0} onChange={toggleAllChecked} />
                            {Object.keys(checked).length ? (
                                <>
                                    <Button variant="contained" color="primary" onClick={onClickCompare}>Compare</Button>
                                </>
                            ) : (
                                    <>
                                        <TextField
                                            label={mode}
                                            variant="outlined"
                                            margin="dense"
                                            onChange={onChangeSearch}
                                            InputProps={{ classes: { input: 'textField' } }}
                                        />
                                        {addSelect('Rank', rank, setRank, sessionInfo.ranks)}
                                        {addSelect('Platform', platform, setPlatform, sessionInfo.platforms)}
                                        {addSelect('Date', date, setDate, sessionInfo.dates)}
                                    </>
                                )}
                        </div>
                        <div>
                            {groups.map((group: SessionData[]) =>
                            {
                                let first = group[0],
                                    key = (first as any)[getSortKey(mode)],
                                    isExpanded = expanded[key],
                                    expand = () => onClickExpand(key);

                                return (
                                    <React.Fragment key={first.resultId}>
                                        {renderHeader(first, mode, isExpanded, expand)}
                                        {isExpanded && group.map(row => (
                                            <div key={row.resultId} className={`${styles.row} ${styles.rowItem}`}>
                                                <Checkbox color="primary" checked={!!checked[row.resultId]} onChange={(e) => setCheckedResult(row.resultId, e.target.checked)} />
                                                <div className={styles.nowrap} onClick={() => p.onSelectSessions([row])}>
                                                    <div className={styles.platform}>{row.resultId + '  '}</div>
                                                    <div className={styles.platform}>{row.testName}</div>
                                                    <div className={styles.date}>{row.platformName}</div>
                                                    <div className={styles.date}>{row.dateString}</div>
                                                </div>
                                            </div>
                                        ))}
                                    </React.Fragment>
                                );
                            })}
                        </div>
                    </div>
                )}
            </Paper>
        </>
    )
};

const Sessions: React.FunctionComponent<Props> = (p: Props) =>
{
    const [sessionData, setSessionData] = useState<{ data: SessionData[], sessionInfo: SessionInfo } | undefined>(undefined);

    useEffect(() =>
    {
        const processData = (d: SessionData[]) =>
        {
            let out: SessionInfo = {
                platforms: {},
                ranks: {},
                dates: {},
                subjects: {}
            }

            d.forEach((it: any) =>
            {
                let date = new Date(it.dateCollected);
                it.date = +date;
                it.dateString = date.toDateString();
                it.rankId = 0;
                it.rank = 'Unknown Rank';
                it.name = `${it.lastName}, ${it.firstName}`;

                out.platforms[it.platformId] = it.platformName;
                out.dates[it.date] = it.dateString;
                out.subjects[it.subjectId] = it.name;
                out.ranks[it.rankId] = it.rank;
            });
            console.log(d);
            setSessionData({ data: d, sessionInfo: out });
        }
        let summary = __DEV__ && localStorage.getItem('_test_summary');
        if (summary)
        {
            processData(JSON.parse(summary));
        }
        else
        {
            fetch('https://eyenetservice20190228013441.azurewebsites.net/v1/result/summary', {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + p.user.token
                }
            }).then(async (result) =>
            {
                let data = await result.json();

                if (__DEV__)
                {
                    localStorage.setItem('_test_summary', JSON.stringify(data));
                }
                processData(data);
            });
        }
    }, [ p.user.token ]);

    return (
        <div className={styles.sessions}>
            {sessionData && <SessionsComponent {...p} data={sessionData.data} sessionInfo={sessionData.sessionInfo} />}
            {!sessionData && <span>Loading...</span>}
        </div>
    );
}

export default Sessions;