import React, { useState, useEffect } from 'react';
import styles from'./SessionDetails.module.css';
import { SessionResults, __DEV__, SessionData } from './Constants';
import { VictoryChart, VictoryLine, VictoryLegend } from 'victory';
const colors = [ '332288', '88CCEE', '44AA99', '117733', '999933', 'DDCC77', 'CC6677', '882255', 'AA4499' ];
interface Props
{
    user: any;
    sessions: SessionData[];
}

const renderLine = (data: any[], y: string, color: string) =>
{
    return (
        <VictoryLine
            key={y}
            data={data}
            x="time"
            y={y}
            style={{ data: { stroke: color, strokeWidth: 1 } }}
        />
    )
}

const Subjects: React.FunctionComponent<Props> = (p: Props) =>
{
    const { sessions } = p,
        token = p.user.token,
        data: any[] = [],
        lines: any[] = [];

    const [ results, setResults ] = useState<SessionResults[] | undefined>(undefined);

    useEffect(() =>
    {
        Promise.all(sessions.map(async (session) =>
        {
            const processData = (resultData: any) =>
            {
                return {
                    session,
                    resultData
                };
            };
            let resultData = __DEV__ && localStorage.getItem(`_test_result_${session.resultId}`);
            if (resultData)
            {
                return processData(JSON.parse(resultData));
            }
            else
            {
                let result = await fetch(`https://eyenetservice20190228013441.azurewebsites.net/v1/result/workloaddata/${session.resultId}`, {
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': 'Bearer ' + token
                    }
                });

                let data = await result.json();

                if (__DEV__)
                {
                    localStorage.setItem(`_test_result_${session.resultId}`, JSON.stringify(data));
                }
                return processData(data);
            }
        })).then((res) =>
        {
            setResults(res);
        });
    }, [token, sessions]);

    let sessionDetails,
        chartTitle;

    if (results)
    {
        results.forEach((result, i) => {
            let session = result.session,
                resultData = result.resultData.slice(0, 100);
            resultData.forEach((it) => {
                let { time, leftValue } = it;
                if (!data[time])
                {
                    data[time] = {};
                }
                data[time][session.resultId] = leftValue;
            });
            lines.push({
                name: session.testName,
                field: session.resultId,
                color: '#' + colors[i]
            });
        });

        data.forEach(d => {
            lines.forEach(line => {
                if (!d[line.field])
                {
                    d[line.field] = 0;
                }
            })
        });

        if (sessions.length === 1)
        {
            let session = sessions[0];
            sessionDetails = (
                <>
                    <div className={styles.name}>{session.name}</div>
                    <div className={styles.rank}>{session.rank}</div>
                </>
            );

            chartTitle = <div className={styles.chartTitle}>{`${session.platformName} ${session.dateString}`}</div>;
        }
        else
        {
            sessionDetails = 'Compare';
        }
    }

    return (
        <div className={styles.sessionDetails}>
            <div className={styles.personDetails}>
                {sessionDetails}
            </div>
            {chartTitle}
            <div className={styles.chart}>
                <VictoryChart
                    animate={{
                        duration: 500,
                        onLoad: { duration: 100 }
                    }}
                >
                    {lines.map(line => renderLine(data, line.field, line.color))}
                    <VictoryLegend
                        // x={125}
                        // y={50}
                        orientation="horizontal"
                        gutter={20}
                        // style={{ border: { stroke: "black" }, title: { fontSize: 20 } }}
                        data={lines.map(line => ({ name: line.name, symbol: { fill: line.color } }))}
                    />
                </VictoryChart>
                </div>
        </div>
    );
}

export default Subjects;